import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/myProcess/api";

@Component({})
export default class CCProcessList extends Vue {
  // 分页、查询信息
  queryCriteria: any = {
    pageSize: 10,
    pageNum: 1
  };
  total: number = 0;
  tableData: Array<any> = [{}, {}, {}];
  loading: boolean = false;
  typeOptions: Array<any> = [];

  created() {
    this.getAllProcessType();
  }
  async getAllProcessType() {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.getAllProcessType);
      if (status) {
        this.typeOptions = data;
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }
  getCCProcess() {}

  getProcessDefine(data: any) {}
  getCurrentNode(data: any) {}
  showTime(data: any) {
    console.log(data);
  }
}
